<template>
  <div
    @dragenter="dragenter($event)"
    @drop="enentDrop($event)"
    @dragleave="dragleave($event)"
    @dragover="dragover($event)"
  >
    <slot />
  </div>
</template>

<script setup>
import { defineEmits } from "vue";

const emit = defineEmits(["onReceiveData"]);

const enentDrop = (e) => {
  e.stopPropagation();
  e.preventDefault();
  var reader = new FileReader();
  reader.readAsText(e.dataTransfer.files[0], "utf-8"); //设置编码
  reader.onload = function () {
    try {
      const json = JSON.parse(this.result);
      emit("onReceiveData", json);
    } catch (error) {
      alert(error.message);
    }
  };
};
const dragleave = (e) => {
  e.stopPropagation();
  e.preventDefault();
};
const dragenter = (e) => {
  e.stopPropagation();
  e.preventDefault();
};
const dragover = (e) => {
  e.stopPropagation();
  e.preventDefault();
};
</script>
